import React, { useState, useMemo } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link,
  useParams,
  Navigate,
} from "react-router-dom";
import {
  Container,
  Typography,
  Box,
  AppBar,
  Toolbar,
  CssBaseline,
  Button,
  Grid,
} from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { useTranslation } from "react-i18next";
import logo from "./assets/logo.jpg";
import "./i18n"; // ← ایمپورت i18n

const BRAND = "moonsa company";

const dronesList = [
  { id: "drone1", name: "MOONSA FED1" },
  { id: "drone2", name: "MOONSA XF-47" },
  { id: "drone3", name: "MOONSA XF-44 Plus" },
  { id: "drone4", name: "MOONSA XF44" },
  { id: "drone5", name: "MOONSA Aryo FF One" },
  { id: "drone6", name: "MOONSA F48 Plus" },
];

function Home({ mode }) {
  const { t, i18n } = useTranslation();

  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };

  return (
    <Container sx={{ mt: 4 }}>
      <Typography
        variant="h3"
        gutterBottom
        sx={{ fontWeight: "bold", textShadow: "0 0 10px gold", color: "#ffd700" }}
      >
        {BRAND}
      </Typography>

      <Typography variant="h5" gutterBottom sx={{ color: "text.primary" }}>
        {t("drones")}
      </Typography>

      <Grid container spacing={3}>
        {dronesList.map((drone) => (
          <Grid item xs={12} sm={6} md={4} key={drone.id}>
            <Box
              sx={{
                border: "2px solid #770681ff",
                borderRadius: 3,
                p: 3,
                textAlign: "center",
                boxShadow:
                  mode === "dark"
                    ? "0 0 20px #6a0dad"
                    : "0 0 20px rgba(106, 13, 173, 0.3)",
                background: mode === "dark" ? "#000000" : "#ffffff",
                transition: "transform 0.3s ease, box-shadow 0.3s ease",
                cursor: "pointer",
                "&:hover": {
                  transform: "scale(1.05) rotate(1deg)",
                  boxShadow: mode === "dark" ? "0 0 30px #6a0dad" : "0 0 30px #bfa64a",
                },
              }}
            >
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", color: "#6a0dad" }}
              >
                {drone.name}
              </Typography>

              <Button
                variant="outlined"
                component={Link}
                to={`/drones/${drone.id}`}
                sx={{
                  borderColor: "#6a0dad",
                  color: "#6a0dad",
                  fontWeight: "bold",
                  "&:hover": {
                    backgroundColor: "#6a0dad",
                    color: "#fff",
                    boxShadow: "0 0 15px #6a0dad",
                  },
                }}
              >
                {t("view")} {drone.name}
              </Button>
            </Box>
          </Grid>
        ))}
      </Grid>

      <Box mt={4}>
        <Typography variant="h5" gutterBottom sx={{ color: "text.primary" }}>
          {t("contact")}
        </Typography>
        <Typography sx={{ color: "text.secondary" }}>
          @Space_sf / @perple_XCH / sullyarmstrong03@gmail.com
        </Typography>

        <Box mt={2}>
          <Button variant="contained" onClick={() => changeLanguage("en")}>
            EN
          </Button>
          <Button variant="contained" onClick={() => changeLanguage("fa")} sx={{ ml: 1 }}>
            FA
          </Button>
          <Button variant="contained" onClick={() => changeLanguage("hi")} sx={{ ml: 1 }}>
            HI
          </Button>
        </Box>
      </Box>
    </Container>
  );
}

function DroneDetail({ mode }) {
  const { t } = useTranslation();
  const { droneId } = useParams();
  const drone = dronesList.find((d) => d.id === droneId);

  if (!drone)
    return (
      <Container sx={{ mt: 4 }}>
        <Typography variant="h5">{t("drone_not_found")}</Typography>
      </Container>
    );

  const images = Array.from({ length: 6 }, (_, i) =>
    process.env.PUBLIC_URL + `/images/${droneId}/img${i + 1}.jpg`
  );

  return (
    <Container sx={{ mt: 4 }}>
      <Typography
        variant="h4"
        gutterBottom
        sx={{ fontWeight: "bold", textShadow: "0 0 8px gold", color: "#ffd700" }}
      >
        {drone.name}
      </Typography>

      <Box sx={{ display: "flex", flexWrap: "wrap", gap: 2, mb: 3 }}>
        {images.map((src, i) => (
          <Box
            key={i}
            component="img"
            src={src}
            alt={`${drone.name} ${i + 1}`}
            sx={{
              width: 220,
              height: 160,
              objectFit: "cover",
              borderRadius: 3,
              border: "2px solid #6a0dad",
              boxShadow: "0 0 15px #6a0dad",
              background: mode === "dark" ? "#000000" : "#ffffff",
            }}
            onError={(e) => (e.target.style.display = "none")}
          />
        ))}
      </Box>

      <Typography
        variant="body1"
        sx={{
          color: "text.secondary",
          fontSize: 18,
          lineHeight: 1.6,
          whiteSpace: "pre-line",
        }}
      >
        {t(`drone_details.${droneId}`, t("description_unavailable"))}
      </Typography>
    </Container>
  );
}

export default function App() {
  const { t } = useTranslation();
  const [mode, setMode] = useState("dark");

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode,
          primary: { main: "#ffd700" },
          secondary: { main: "#ffd700" },
          background: {
            default: mode === "dark" ? "#000000" : "#f5f5f5",
            paper: mode === "dark" ? "#000000" : "#ffffff",
          },
          text: {
            primary: mode === "dark" ? "#ffd700" : "#222222",
            secondary: mode === "dark" ? "#ffd700" : "#555555",
          },
        },
        typography: { fontFamily: "Cinzel, serif" },
      }),
    [mode]
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router basename="/perpleXCH">
        <AppBar
          position="static"
          sx={{
            background:
              mode === "dark"
                ? "linear-gradient(135deg, #000000, #1a0f2e)"
                : "linear-gradient(135deg, #e0e0e0, #ffffff)",
            borderBottom: `2px solid ${mode === "dark" ? "#ffd700" : "#bfa64a"}`,
            boxShadow: `0 0 15px ${mode === "dark" ? "gold" : "#bfa64a"}`,
          }}
        >
          <Toolbar
            sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}
          >
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Box
                component="img"
                src={logo}
                alt={`${BRAND} logo`}
                sx={{ height: 40, borderRadius: "8px", mr: 1 }}
              />
              <Typography
                variant="h6"
                component={Link}
                to="/"
                sx={{ textDecoration: "none", color: "#ffd700", fontWeight: "bold" }}
              >
                {BRAND}
              </Typography>
            </Box>

            <Button
              onClick={() => setMode((prev) => (prev === "dark" ? "light" : "dark"))}
              sx={{
                backgroundColor: "#ffd700",
                color: mode === "dark" ? "#000" : "#222",
                fontWeight: "bold",
                "&:hover": { backgroundColor: "#e6c200" },
              }}
            >
              {mode === "dark" ? t("light_mode") : t("dark_mode")}
            </Button>
          </Toolbar>
        </AppBar>

        <Routes>
          <Route path="/" element={<Home mode={mode} />} />
          <Route path="/drones/:droneId" element={<DroneDetail mode={mode} />} />
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}
